@charset "UTF-8";
/**
***** Button *****
*/
.btn {
  vertical-align: middle;
}
.btn.btn-primary {
  background-color: #027afe;
  border-color: #027afe;
  box-shadow: 0 0 4px #027afe;
}
.btn.btn-primary:hover {
  background-color: #0167d6;
  border-color: #0167d6;
}
.btn.btn-secondary {
  background-color: #82868b;
  border-color: #82868b;
  box-shadow: 0 0 4px #82868b;
}
.btn.btn-secondary:hover {
  background-color: #6e7276;
  border-color: #6e7276;
}
.btn.btn-success {
  background-color: #28c76f;
  border-color: #28c76f;
  box-shadow: 0 0 4px #28c76f;
}
.btn.btn-success:hover {
  background-color: #21a55c;
  border-color: #21a55c;
}
.btn.btn-info {
  background-color: #00cfe8;
  border-color: #00cfe8;
  box-shadow: 0 0 4px #00cfe8;
  color: #ffffff;
}
.btn.btn-info:hover {
  background-color: #00abbf;
  border-color: #00abbf;
}
.btn.btn-warning {
  background-color: #ff9f43;
  border-color: #ff9f43;
  box-shadow: 0 0 4px #ff9f43;
  color: #ffffff;
}
.btn.btn-warning:hover {
  background-color: #ff8a1a;
  border-color: #ff8a1a;
}
.btn.btn-danger {
  background-color: #ea5455;
  border-color: #ea5455;
  box-shadow: 0 0 4px #ea5455;
}
.btn.btn-danger:hover {
  background-color: #e63031;
  border-color: #e63031;
}
.btn.btn-light {
  background-color: #ebebeb;
  border-color: #ebebeb;
  box-shadow: 0 0 4px #ebebeb;
}
.btn.btn-light:hover {
  background-color: #d7d7d7;
  border-color: #d7d7d7;
}
.btn.btn-dark {
  background-color: #191f30;
  border-color: #191f30;
  box-shadow: 0 0 4px #191f30;
}
.btn.btn-dark:hover {
  background-color: #0b0e15;
  border-color: #0b0e15;
}
.btn.btn-outline-primary {
  color: #027afe;
  border-color: #027afe;
}
.btn.btn-outline-primary:hover {
  background-color: #027afe;
  box-shadow: 0 0 4px #027afe;
  color: #ffffff;
}
.btn.btn-outline-primary:focus {
  box-shadow: none;
}
.btn.btn-outline-secondary {
  color: #82868b;
  border-color: #82868b;
}
.btn.btn-outline-secondary:hover {
  background-color: #82868b;
  box-shadow: 0 0 4px #82868b;
  color: #ffffff;
}
.btn.btn-outline-secondary:focus {
  box-shadow: none;
}
.btn.btn-outline-success {
  color: #28c76f;
  border-color: #28c76f;
}
.btn.btn-outline-success:hover {
  background-color: #28c76f;
  box-shadow: 0 0 4px #28c76f;
  color: #ffffff;
}
.btn.btn-outline-success:focus {
  box-shadow: none;
}
.btn.btn-outline-info {
  color: #00cfe8;
  border-color: #00cfe8;
}
.btn.btn-outline-info:hover {
  background-color: #00cfe8;
  box-shadow: 0 0 4px #00cfe8;
  color: #ffffff;
}
.btn.btn-outline-info:focus {
  box-shadow: none;
}
.btn.btn-outline-warning {
  color: #ff9f43;
  border-color: #ff9f43;
}
.btn.btn-outline-warning:hover {
  background-color: #ff9f43;
  box-shadow: 0 0 4px #ff9f43;
  color: #ffffff;
}
.btn.btn-outline-warning:focus {
  box-shadow: none;
}
.btn.btn-outline-danger {
  color: #ea5455;
  border-color: #ea5455;
}
.btn.btn-outline-danger:hover {
  background-color: #ea5455;
  box-shadow: 0 0 4px #ea5455;
  color: #ffffff;
}
.btn.btn-outline-danger:focus {
  box-shadow: none;
}
.btn.btn-outline-light {
  color: #ebebeb;
  border-color: #ebebeb;
}
.btn.btn-outline-light:hover {
  background-color: #ebebeb;
  box-shadow: 0 0 4px #ebebeb;
  color: #ffffff;
}
.btn.btn-outline-light:focus {
