}
.btn i {
  font-size: 0.9em;
}
.btn.icon-left i {
  margin-right: 0.3em;
}
.btn.icon-right i {
  margin-left: 0.3em;
}

/**
***** Form *****
*/
.form-control, .form-select,
.form-control-plaintext {
  color: #625f6e;
  font-size: 0.9em;
  /* padding: 18px 12px; */
}

.form-control-sm {
  font-size: 0.8em;
}

.form-control-lg {
  font-size: 1.2em;
}

.datepicker td, .datepicker th {
  font-size: 0.9em;
  padding: 0.4em;
}

.input-daterange {
  padding: 0 !important;
}

.form-control:focus,
.form-select:focus,
.form-check-input:focus {
  box-shadow: 0 0 6px #add4ff;
  border-color: #027afe;
}

.form-control-plaintext:focus {
  outline: none;
}

.form-label,
.form-text {
  color: #625f6e;
  font-size: 0.8em;
  margin-bottom: 0.2em;
}

.form-label ~ small {
  font-style: italic;
}

.form-select {
  color: #625f6e;
}

.form-check-input:checked {
  background-color: #027afe;
  border-color: #027afe;
  box-shadow: 0 0 6px #027afe;
}

.input-group-join .form-control {
  transition: 0.1s border;
  border-right: none;
}
.input-group-join .input-group-text {
  transition: 0.1s border;
  border-left-width: 0 !important;
}

.input-group-join:focus-within .form-control, .input-group-join:focus-within .input-group-text {
  box-shadow: none;
  border-color: #027afe;
}

.cursor-pointer {
  cursor: pointer;
}

.was-validated .form-control:invalid {
  border-color: #ea5455 !important;
}
.was-validated .form-control:invalid:focus {
  box-shadow: none !important;
}
.was-validated .form-control:invalid + .input-group-text {
  border-color: #ea5455 !important;
}
.was-validated .input-group-text, .was-validated .form-control:valid {
  border-color: #28c76f !important;
}
.was-validated .input-group-text:focus, .was-validated .form-control:valid:focus {
  box-shadow: none !important;
}

.input-group-text {
  background: #ffffff;
  color: #625f6e;
}

.input-group-text.light {
  background: #f7f7f7;
  color: #625f6e;
}

.input-group .btn {
  z-index: 2;
}

.input-group .btn i {
  text-align: center;
  display: block;
  padding: 2px;
}

.form-switch.lg {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.form-switch.lg label {
  display: block;
}
.form-switch.lg .form-check-input {
  margin-top: 0;
  height: 25px;
  width: 40px;
  margin-left: 0px;
  background-color: #f7f7f7;
}
.form-switch.lg .form-check-input:checked {
  background-color: #027afe;
}

.form-check-inline.lg .form-check-input.lg {
  height: 20px;
  width: 20px;
}
.form-check-inline.lg label {
  margin-top: 2px;
}

/**
***** Dropdown *****
*/
.dropdown-menu {
  border-width: 0;
  box-shadow: 0 0 2px 0.3px #e9e9e9;
}
.dropdown-menu .menu-header * {
  border-bottom: 1px solid #e9e9e9;
  padding-bottom: 5px;
  font-weight: 500;
  font-size: 1em;
  color: #625f6e;
}
.dropdown-menu.small {
  width: 150px;
  top: 8px !important;
}
.dropdown-menu.medium {
  width: 350px;
  top: 20px !important;
}
.dropdown-menu .menu-header a:hover {
  background: none;
}
.dropdown-menu .menu-content {
  position: relative;
  max-height: 300px;
  overflow: hidden;
}
.dropdown-menu .menu-content a {
  padding: 8px 10px;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  text-decoration: none;
  border-bottom: 1px solid #f1f1f1;
  width: 100%;
  position: relative;
}
.dropdown-menu .message-image {
  width: 55px;
}
.dropdown-menu .message-icon {
  flex-shrink: 0;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
}
.dropdown-menu .message-content {
  overflow: hidden;
  width: 100%;
}
.dropdown-menu .message-content.read .subject,
.dropdown-menu .message-content.read .body {
  color: #b8c2cc;
}
.dropdown-menu .menu-content a:nth-last-child(1) {
  border-bottom: none;
}
.dropdown-menu .menu-content a:hover {
  background: #f1f1f1;
}
.dropdown-menu .menu-content .subject {
  color: #191f30;
  font-weight: 500;
  margin: 0 10px;
  line-height: 1em;
  margin-bottom: 5px;
}
.dropdown-menu .menu-content .body,
.dropdown-menu .menu-content .description, .dropdown-menu .menu-content .time {
  margin: 0 10px;
  line-height: 1.2em;
  font-size: 0.8em;
  color: #625f6e;
}
.dropdown-menu .menu-content .time {
  margin-top: 5px;
  float: right;
  font-style: italic;
  color: #b8c2cc;
  font-weight: 500;
  font-size: 0.65em;
}
.dropdown-menu .menu-content .description i {
  margin-right: 10px;
  line-height: 1em;
}

/**
***** Card *****
*/
.card-body.small-padding {
  padding: 0;
}

.card {
  box-shadow: 0 4px 24px 0 #d8d8d8;
  margin-bottom: 2em !important;
}
.card .card-header {
  border-bottom: none;
  padding: 1em;
  background-color: transparent;
}
.card .card-header h4 {
  margin-bottom: 0;
  font-size: 1.2em;
  font-weight: 500;
  color: #625f6e;
}
.card .card-header + .card-body {
  padding-top: 0;
}
.card .card-footer {
  border-top: none;
}

.card:not([class*=border]) {
  border: none;
}

