/**
***** Alert *****
*/
.modal .modal-content {
  overflow: visible;
}
.modal-title {
  font-weight: 500;
}
.modal-header {
  background-color: #f7f7f7;
  border-bottom: none;
  height: 3em;
}
.modal-header .btn-close {
  position: relative;
  transform: translate(25px, -18px);
  background: #ffffff;
  opacity: 1;
  box-shadow: -2px 5px 20px #b8c2cc;
  transition: all 0.2s;
  border-radius: 25%;
}
.modal-header .btn-close:hover {
  border-radius: 50%;
  transform: translate(25px, -18px) rotate(90deg);
  box-shadow: none;
}
.modal-header .btn-close::after {
  content: "";
  font-family: "themify";
  text-align: center;
  display: block;
  line-height: 1em;
  color: #625f6e;
  font-size: 0.8em;
}
.modal-footer {
  border-top-color: #f1f1f1;
}

/**
***** Progress *****
*/
.progress-wrapper {
  margin: -5px 0 15px 0;
}

.progress-wrapper h4 {
  font-size: 1em;
  font-weight: 100;
  color: #625f6e;
}

.progress-bar-small {
  height: 10px;
}

/**
***** Accordion *****
*/
.accordion.accordion-space .accordion-item {
  margin-bottom: 0.5em;
  border-radius: 0.25em;
  overflow: hidden;
}
.accordion.accordion-space .accordion-item:not(:first-of-type) {
  border-top: 1px solid rgba(0, 0, 0, 0.125);
}
.accordion.accordion-space .accordion-button {
  border-radius: 0;
}

.accordion-item .accordion-button {
  color: #625f6e;
  padding: 0.7em;
}
.accordion-item .accordion-button::after {
  content: "";
  font-family: "themify";
  background: none;
  font-size: 0.7em;
  height: unset;
  width: unset;
}
.accordion-item .accordion-button:not(.collapsed) {
  color: #191f30;
  background-color: #f1f1f1;
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.13);
}
.accordion-item .accordion-button:not(.collapsed)::after {
  color: #027afe;
  font-weight: 500;
}
.accordion-item .accordion-button:focus {
  border-color: none;
  box-shadow: none;
}

/**
***** Tabs *****
*/
.nav-tabs .nav-link,
.nav-pills .nav-link {
  color: #027afe;
}
.nav-tabs .nav-link.disabled,
.nav-pills .nav-link.disabled {
  color: #b8c2cc;
}

.nav-pills .nav-link.active {
  background-color: #027afe;
}

.tab-pane {
  margin-top: 1em;
}

.nav.flex-column ~ .tab-content .tab-pane {
  margin-top: 0;
}

/**
***** Table *****
*/
table.table {
  color: #625f6e;
}
table.table thead {
  background-color: #f7f7f7;
}
table.table thead tr th {
  font-weight: 500;
}
table.table > :not(:last-child) > :last-child > * {
  border-bottom-color: #b8c2cc;
}

